<template>
	<div>
		<el-tabs v-model="activeName" @tab-click="clickTab">
		    <el-tab-pane label="USDT充值记录" name="first"></el-tab-pane>
		    <el-tab-pane label="CPF充值记录" name="second"></el-tab-pane>
		  </el-tabs>
		<div class="searchs">
		  <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
			<el-row :gutter="24">
			  <el-col>
				<el-form-item label="账号">
				  <el-input v-model="searchs.keyword_search_value" placeholder="会员手机号"></el-input>
				</el-form-item>
				<el-form-item label="提交时间">
				  <el-date-picker
					v-model="addtime"
					type="daterange"
					align="right"
					unlink-panels
					range-separator="至"
					start-placeholder="开始日期"
					end-placeholder="结束日期"
					:picker-options="pickerOptions"
				  />
				</el-form-item>
				<el-form-item>
				  <el-button type="primary" native-type="submit">查询</el-button>
				  <!-- <el-button type="danger" @click="cencels">批量取消</el-button>
				  <el-button type="primary" @click="removeAll">批量删除</el-button> -->
				</el-form-item>
			  </el-col>
			  <el-col>
				充值成功：<span style="color: red;">{{ data.sum_ok_money }}元</span> 充值失败：<span style="color: red;"
				  >{{ data.sum_no_money }}元</span
				>
				未处理：<span style="color: red;">{{ data.sum_on_money }}元</span>
			  </el-col>
			</el-row>
		  </el-form>
		</div>
		<el-table :data="tableData" class="list" v-loading="loading" @selection-change="handleSelectionChange">
		  <el-table-column type="selection" width="55"> </el-table-column>
		  <el-table-column prop="id" label="订单号" align="center" />
		  <el-table-column prop="mobile" label="账号" align="center" width="150" />
		  <el-table-column prop="name" label="姓名" align="center" />
		  <el-table-column prop="money" label="充值金额" align="center" />
		  <el-table-column prop="channel" label="支付方式" align="center" />
		  <!-- <el-table-column prop="status_name" label="状态" align="center" /> -->
		  <el-table-column prop="voucher" label="凭证" align="center">
			  <template slot-scope="scope">
				  <el-popover placement="top-start" title="" trigger="hover">
					<img :src="scope.row.voucher" alt="" style="width: 300px;height: 300px" v-if="scope.row.voucher">
					<img slot="reference" :src="scope.row.voucher" style="width: 30px;height: 30px" v-if="scope.row.voucher">
				  </el-popover>
				  
			  </template>
		  </el-table-column>
		  <!-- <el-table-column prop="remark" label="备注" align="center" /> -->
		  <el-table-column prop="addtime_date" label="提交时间" align="center" width="170" />
		  <el-table-column label="操作" width="280">
			<template slot-scope="scope">
			  <el-button
				v-if="scope.row.status === 'S' || scope.row.status === 'D'"
				@click="setSuccess(scope.row)"
				type="success"
				size="mini"
				>成功
			  </el-button>
			  <el-button
				v-if="scope.row.status === 'S' || scope.row.status === 'D'"
				@click="setNot(scope.row)"
				type="warning"
				size="mini"
				>失败
			  </el-button>
			  <el-button v-if="scope.row.status === 'S'" @click="setMiss(scope.row)" type="primary" size="mini">
				忽略
			  </el-button>
			  <el-button
				v-if="scope.row.status === 'S' || scope.row.status === 'D'"
				@click="cancel(scope.row)"
				type="danger"
				size="mini"
				>取消
			  </el-button>
			  <el-col v-if="scope.row.status === 'Y' || scope.row.status === 'N' || scope.row.status === 'C'">
				无
			  </el-col>
			</template>
		  </el-table-column>
		</el-table>
		<el-pagination
		  background
		  layout="total, prev, pager, next"
		  :total="count"
		  :current-page="page_curren"
		  :hide-on-single-page="false"
		  :page-size = 'page_num'
		  @current-change="currentChange"
		  class="pagination"
		/><!-- page-size="50" -->
		<el-dialog title="会员详情" :visible.sync="dialogFormVisible">
		  <el-form :model="detail">
			<el-form-item label="活动名称" label-width="80">
			  <el-input v-model="detail" autocomplete="off"></el-input>
			</el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
			<el-button @click="dialogFormVisible = false">取 消</el-button>
			<el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
		  </div>
		</el-dialog>
	</div>
</template>
<script>
import dayjs from 'dayjs'
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        keyword_search_value: '',
        begin_addtime: '',
        end_addtime: '',
		status: '',
        channel: '',
      },
      config: {
        status: {},
        channel: {},
      },
      loading: false,
      detail: {},
      data: {},
      tableData: [],
      count: 0,
      page_curren: 1,
	  page_num:10,
      addtime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
      ids: [],
	  activeName: 'first'
    }
  },
  watch: {
    addtime(val) {
      if (val) {
        this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
        this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
      } else {
        this.searchs.begin_addtime = ''
        this.searchs.end_addtime = ''
      }
    },
  },
	mounted() {
		this.FetchList()
	},
	methods: {
		clickTab(tab,event){
			this.FetchList();
		},
		cencels() {
		  if (this.ids.length < 1) {
			Message.error('未选择数据')
			return false
		  }
		  this.$confirm('此操作将会直接使先中订单失效, 是否继续?', '警告', {
			confirmButtonText: '确定',
			cancelButtonText: '取消',
			type: 'warning',
		  }).then(() => {
			this.loading = true
			Fetch('/fin/invest/cancels', { ids: this.ids })
			  .then(() => {
				this.loading = false
				Message.success('操作成功')
				this.FetchList()
			  })
			  .catch(() => {
				this.loading = false
			  })
		  })
		},
		FetchList() {
			this.loading = true;
			var type = 2;
			if(this.activeName == 'first'){
				type = 2
			}else if(this.activeName == 'second'){
				type = 3
			}
			Fetch('/fin/invest/getlist', {
				type:type,
				page_curren: this.page_curren,
				...this.searchs,
			})
			.then(r => {
				this.loading = false
				this.data = r.data
				this.count = r.data.count
				this.page_curren = Math.floor(r.data.page_curren)
				this.tableData = r.data.list
				this.config = r.data.config
				this.page_num = r.data.page_num
			})
			.catch(() => (this.loading = false))
		},
		currentChange(page) {
		  this.page_curren = page
		  this.FetchList()
		},
		onSubmit() {
		  this.page_curren = 1
		  this.FetchList()
		},
		setSuccess(row) {
		  this.$confirm('是否确认将状态修改为成功?', undefined, {
			type: 'warning',
			beforeClose: (action, instance, done) => {
			  if (action === 'confirm') {
				instance.confirmButtonLoading = true
				var link = ''
				if(this.activeName == 'first'){
					link = '/fin/invest/verify'
				}else if(this.activeName == 'second'){
					link = '/fin/invest/audit'
				}
				if(!link){
					return;
				}
				Fetch(link, { id: row.id, status: 'Y' })
				  .then(() => {
					instance.confirmButtonLoading = false
					Message.success('操作成功')
					this.FetchList()
					done()
				  })
				  .catch(() => {
					instance.confirmButtonLoading = false
					Message.error('操作失败')
				  })
			  } else {
				done()
			  }
			},
		  })
		},
		setNot(row) {
		  this.$prompt('请输入失败原因', '是否确认将状态修改为失败?', {
			// type: 'warning',
			// beforeClose: (action, instance, done) => {
			// if (action === 'confirm') {
			//   instance.confirmButtonLoading = true
			//   // Fetch('/fin/invest/verify', { id: row.id, status: 'N', remark: '312332' })
			//   //   .then(() => {
			//   instance.confirmButtonLoading = false
			//   Message.success('操作成功')
			//   this.FetchList()
			//   done()
			//   //   })
			//   //   .catch(() => {
			//   //     instance.confirmButtonLoading = false
			//   //     Message.error('操作失败')
			//   //   })
			// } else {
			//   done()
			// }
			// },
			confirmButtonText: '确定',
			cancelButtonText: '取消',
		  }).then(({ value }) => {
			Fetch('/fin/invest/verify', { id: row.id, status: 'N', fail_tips: value })
			  .then(() => {
				Message.success('操作成功')
				this.FetchList()
			  })
			  .catch(() => {
				Message.error('操作失败')
			  })
		  })
		},
		setMiss(row) {
		  this.$confirm('是否确认将状态修改为忽略?', undefined, {
			type: 'warning',
			beforeClose: (action, instance, done) => {
			  if (action === 'confirm') {
				instance.confirmButtonLoading = true
				Fetch('/fin/invest/verify', { id: row.id, status: 'D' })
				  .then(() => {
					instance.confirmButtonLoading = false
					Message.success('操作成功')
					this.FetchList()
					done()
				  })
				  .catch(() => {
					instance.confirmButtonLoading = false
					Message.error('操作失败')
				  })
			  } else {
				done()
			  }
			},
		  })
		},
		cancel(row) {
		  this.$confirm('此操作将会直接使订单失效, 是否继续?', '警告', {
			confirmButtonText: '确定',
			cancelButtonText: '取消',
			type: 'warning',
		  }).then(() => {
			this.loading = true
			Fetch('/fin/invest/verify', { id: row.id, status: 'C' })
			  .then(() => {
				this.loading = false
				Message.success('操作成功')
				this.FetchList()
			  })
			  .catch(() => {
				this.loading = false
			  })
		  })
		},
		removeAll() {
		  if (this.ids.length < 1) {
			Message.error('未选中数据')
			return
		  }
		  this.loading = true
		  Fetch('/fin/invest/remove', { id: this.ids })
			.then(() => {
			  Message.success('删除成功')
			  this.FetchList()
			  this.loading = false
			})
			.catch(() => (this.loading = false))
		},
		handleSelectionChange(val) {
		  this.ids = []
		  if (val) {
			val.forEach(item => {
			  this.ids.push(item.id)
			})
		  }
		},
	},
}
</script>
<style lang="less" scoped>
.searchs {
  background-color: #f2f2f2;
  border-left: 5px solid #1aa094;
  padding: 14px 0 14px 15px;
  color: #606266;
  font-size: 16px;

  .form-search {
    /deep/ .el-input__inner {
      width: 150px;
    }

    /deep/ .el-range-editor.el-date-editor--daterange {
      width: 300px;
    }
  }
}

.pagination {
  text-align: right;
  margin-top: 20px;
}

.list {
  width: 100%;
  margin-top: 20px;
  text-align: center;

  /deep/ .el-table__header-wrapper {
    th {
      background-color: #f2f2f2;
    }
  }
}
</style>
